import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataKondisiBarang } from "../../actions/datamaster_action";
import { ReanderField } from "../../components/helpers/field";
const maptostate = (state) => {
  return {
    datakondisi: state.datamaster.getDataKondisiBarang,
    setHancurBarang: state.databarang.setHancurBarang,
  };
};
class FIlterPencarianMember extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      kondisi_barang: "kosong",
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
  }
  
  render() {
    return (
        <div className="row">
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getKodeMember(e.target.value)}
              name="kode_member"
              component={ReanderField}
              type="text"
              label="Kode Member"
              placeholder="Masukan Kode Member"
            />
          </div>
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getNamaMember(e.target.value)}
              name="nama_member"
              component={ReanderField}
              type="text"
              label="Nama Member"
              placeholder="Masukan Nama Member"
            />
          </div>
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getNohp(e.target.value)}
              name="no_hp"
              component={ReanderField}
              type="text"
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getAlamat(e.target.value)}
              name="alamat"
              component={ReanderField}
              type="text"
              label="Alamat"
              placeholder="Masukan Alamat"
            />
          </div>
        </div>
    );
  }
}

FIlterPencarianMember = reduxForm({
  form: "FIlterPencarianMember",
  enableReinitialize: true,
})(FIlterPencarianMember);
export default connect(maptostate, null)(FIlterPencarianMember);
