import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../components/panel/panel.jsx";
import { connect } from "react-redux";
import Tabel from "../../components/helpers/tabel.jsx";
import FIlterPencarianMember from "./FIlterPencarianMember.jsx";
import { postData, getData, putData, getDataNoTimeOut, postDataNoTImeOut } from "../../config/axios.jsx";
import { ToastNotification } from "../../components/helpers/notification.jsx";
import { hideModal, showModal } from "../../actions/datamaster_action.jsx";
import ModalGlobal from "../../components/helpers/ModalGlobal.jsx";
import FormTambahMember from "./modal/FormTambahMember.jsx";
import { reset } from "redux-form";
import { showEditMember } from "../../actions/member_action.jsx";
import { setCamera } from "../../actions/databarang_action.jsx";
import { dataURLtoFile, formatDate, getImage, postImage } from "../../components/helpers/function.jsx";
import * as moment from "moment-timezone";
import Loading from 'react-fullscreen-loading';

class DataMember extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      formEdit: false,
      loadingData: false,
      tgl_lahir: "",
      datamember: [],
      columns: [
        {
          dataField: "kode_member",
          text: "Kode Member",
        },
        {
          dataField: "nama_customer",
          text: "Nama Member",
        },
        {
          dataField: "no_hp",
          text: "No Hp",
        },
        {
          dataField: "tgl_lahir",
          text: "Tgl Lahir",
        },
        {
          dataField: "point",
          text: "Point",
          formatter: (value, row) => {
            return value || 0;
          },
        },
        {
          dataField: "alamat_customer",
          text: "Alamat",
          formatter: (value, row) => {
            return (
              value + " " + row.kecamatan + " " + row.kelurahan + " " + row.kota
            );
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
           
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditMember(row)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }
  async formEditMember(row) {
    // console.log(row)
    let hasil = "";
    await getImage('foto_member/'+row.kode_member)
      .then((res) => {
        hasil = {
          kode_member: row.kode_member,
          nama_customer: row.nama_customer,
          no_hp: row.no_hp,
          kecamatan: row.kecamatan,
          kelurahan: row.kelurahan,
          kota: row.kota,
          tgl_lahir: row.tgl_lahir,
          alamat_customer: row.alamat_customer,
          dataimage: res,
        };
      })
      .catch((err) =>{
          hasil = {
            kode_member: row.kode_member,
            nama_customer: row.nama_customer,
            no_hp: row.no_hp,
            kecamatan: row.kecamatan,
            kelurahan: row.kelurahan,
            kota: row.kota,
            tgl_lahir: row.tgl_lahir,
            alamat_customer: row.alamat_customer,
          }
        }
      );
    this.props.dispatch(showEditMember(hasil));
    this.props.dispatch(showModal());
    this.props.dispatch(setCamera(false));
    this.setState({
      isEdit: true,
    });
  }

  componentDidMount(){
    this.setState({
      isLoading: true
    })
    getDataNoTimeOut("member/get/all")
      .then((res) => {
        this.setState({
          isLoading: false
        })
        this.setState({
          datamember: res.data,
        });
      })
      .catch((err) => {
        this.setState({
          isLoading: false
        })
        ToastNotification("info", "Data Kosong");
        this.setState({
          datamember: [],
        });
      });
  }
  getKodeMember(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      this.setState({
        isLoading: true
      })
      getData("member/get/by-kode-member/" + e)
        .then((res) => {
          if (res.data.length === 0) {
            ToastNotification("info", "Kode Member Tidak Di Temukan");
            this.setState({
              datamember: [],
              isLoading: false
            });
          } else {
            this.setState({
              isLoading: false,
              datamember: res.data,
            });
          }
        })
        .catch((err) => {
          ToastNotification("info", "Kode Member Tidak Ada Ditemukan");
          this.setState({
            datamember: [],
          });
        });
    }, 1500);
  }
  getNamaMember(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      let pencarian = {
        nama_customer: e,
      };
      postData("member/get/by-nama/", pencarian)
        .then((res) => {
          console.log(res.data);
          if (res.data.length === 0) {
            ToastNotification("info", "Nama Tidak Di Temukan");
            this.setState({
              datamember: [],
            });
          } else {
            this.setState({
              datamember: res.data,
            });
          }
        })
        .catch((err) => {
          ToastNotification("info", "Nama Tidak Tidak Ditemukan");
          this.setState({
            datamember: [],
          });
        });
    }, 1500);
  }
  getAlamat(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      let pencarian = {
        alamat: e,
      };
      postData("member/get/by-alamat", pencarian)
        .then((res) => {
          console.log(res.data);
          if (res.data.length === 0) {
            ToastNotification("info", "Alamat Tidak Di Temukan");
            this.setState({
              datamember: [],
            });
          } else {
            this.setState({
              datamember: res.data,
            });
          }
        })
        .catch((err) => {
          ToastNotification("info", "Alamat Tidak Di Temukan");
          this.setState({
            datamember: [],
          });
        });
    }, 1500);
  }
  getNohp(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      let pencarian = {
        hp: e,
      };
      postData("member/get/by-hp", pencarian)
        .then((res) => {
          console.log(res.data);
          if (res.data.length === 0) {
            ToastNotification("info", "No Hp Tidak Di Temukan");
            this.setState({
              datamember: [],
            });
          } else {
            this.setState({
              datamember: res.data,
            });
          }
        })
        .catch((err) => {
          ToastNotification("info", "No Hp Tidak Di Temukan");
          this.setState({
            datamember: [],
          });
        });
    }, 1500);
  }
  tambahMember() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showEditMember(false));
    this.setState({
      isEdit: false,
    });
    this.props.dispatch(showModal());
  }

  simpanMember(data) {
    // console.log(data);
    this.setState({
      isLoading: true,
    });
    let data_post = {
      nama_customer: data.nama_customer,
      alamat_customer: data.alamat_customer,
      no_hp: data.no_hp,
      kecamatan: data.kecamatan,
      kelurahan: data.kelurahan,
      kota: data.kota,
      tgl_lahir:  formatDate(moment(new Date(data.tgl_lahir)).format("YYYY-MM-DD")),
      // dataimage: data.dataimage,
    };


    this.state.isEdit
      ? putData("member/edit/by-kode-member/" + data.kode_member, data_post)
          .then((res) => {
            this.setState({
              isLoading: false,
              isEdit: false,
            });
            ToastNotification("success", "Data Berhasil Diedit");
            this.props.dispatch(reset("FormTambahMember"));
            this.props.dispatch(hideModal());
            getData("member/get/by-kode-member/" + data.kode_member)
              .then((res) => {
                if(data.datagambarlama !== data.dataimage){
                  const file = data.dataimage !== undefined  ? dataURLtoFile(data.dataimage) : undefined ;
                  if(file !== undefined){
                    postImage(file,'foto_member/'+data.kode_member).then((res)=>{
                    }).catch((err)=>{
                      ToastNotification('info', "upload Foto Gagal, Silahkan Edit Coba Upload Kembali")
                    })
                  }
                }
            
                if (res.data.length === 0) {
                  ToastNotification("info", "Kode Member Tidak Di Temukan");
                } else {
                  this.setState({
                    datamember: res.data,
                  });
                }
              this.props.dispatch(setCamera(false));
              })
              .catch((err) => {
                ToastNotification("info", "Kode Member Tidak Ada Ditemukan");
              });
          })
          .catch((err) => {
            this.setState({
              isLoading: false,
            });
            ToastNotification("info", "Data Gagal Diedit");
          })
      : postDataNoTImeOut("member/add", data_post)
          .then((res) => {
            const file = data.dataimage !== undefined  ? dataURLtoFile(data.dataimage) : undefined ;
            if(file !== undefined){
              postImage(file,'foto_member/'+res.data.kode_member).then((res)=>{
              }).catch((err)=>{
                ToastNotification('info', "upload Foto Gagal, Silahkan Edit Coba Upload Kembali")
              })
            }
            ToastNotification("success", "Data Member Berhasil Disimpan");
            this.props.dispatch(reset("FormTambahMember"));
            this.props.dispatch(hideModal());
            this.setState({
              isLoading: false,
            });
            let tgl = {
              tgl_awal : formatDate(moment(new Date()).format("YYYY-MM-DD")),
              tgl_akhir :formatDate(moment(new Date()).format("YYYY-MM-DD"))
            }
            console.log(tgl);
            postData("member/get/by-tanggal",tgl)
              .then((res) => {
              // console.log(res.data.detail);
                let datacustomer = [];
                res.data.forEach(element => {
                  element.detail.forEach(rowdata => {
                    let row = {
                      kode_member : rowdata.kode_member,
                      nama_customer : rowdata.nama_customer,
                      alamat_customer : rowdata.alamat_customer,
                      no_hp : rowdata.no_hp,
                      kecamatan : rowdata.kecamatan,
                      kelurahan : rowdata.kelurahan,
                      kota : rowdata.kota,
                      point : rowdata.point,
                      tgl_lahir : rowdata.tgl_lahir,
                    }
                    datacustomer.push(row)
                  });
                });
                this.setState({
                  datamember: datacustomer,
                });
              this.props.dispatch(setCamera(false));
            })
            .catch((err) => {
              ToastNotification("info", "Kode Member Tidak Ada Ditemukan");
            });
      
          })
          .catch((err) => {
            this.setState({
              isLoading: false,
            });
            ToastNotification("info", "Data Member Gagal Disimpan");
          });
  }
  setStartDate(date) {
    this.setState({
      tgl_lahir: new Date(date),
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Member</Link>
          </li>
          <li className="breadcrumb-item active">Data Member</li>
        </ol>
        <h1 className="page-header">Data Member </h1>

        <Panel>
          <PanelHeader>Filter Pencarian Member</PanelHeader>
          <br />
          <div className="container">
            <FIlterPencarianMember
              getNamaMember={(data) => this.getNamaMember(data)}
              getKodeMember={(data) => this.getKodeMember(data)}
              getNohp={(data) => this.getNohp(data)}
              getAlamat={(data) => this.getAlamat(data)}
            />
          </div>
        </Panel>
        <Panel>
          <PanelHeader>
            {this.state.loadingData ? (
              <>
                {" "}
                <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang Mencari
                Data Member{" "}
              </>
            ) : (
              "Data Member"
            )}
          </PanelHeader>
          <br />

          <div className="container">
            <Tabel
              keyField="kode_Member"
              tambahData={true}
              btnText="Tambah Data Member"
              handleClick={(data) => this.tambahMember(data)}
              onChange={this.setStartDate}
              data={this.state.datamember}
              columns={this.state.columns}
              empty={true}
            />
          </div>
          <br />

          {/* End Tambah Data Member  */}
          <ModalGlobal
            // size="L"
            title={
              this.state.isEdit ? "Edit Data Member" : "Tambah Data Member"
            }
            content={
              <FormTambahMember
                tgl_lahir={this.state.tgl_lahir}
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.simpanMember(data)}
              />
            }
          />
        </Panel>
        {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

export default connect(null)(DataMember);
