import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { HiidenFiled, InputDate, ReanderField } from "../../../components/helpers/field";
import userfound from "../../../assets/img/userfound.jpg";
import { setCamera } from "../../../actions/databarang_action";
import { convertBase64, getTglSystem } from "../../../components/helpers/function";
import { ToastNotification } from "../../../components/helpers/notification";
import { postData2 } from "../../../config/axios";
const maptostate = (state) => {
  // console.log(state.datamember.showEditMember.tgl_lahir);
  if (state.datamember.showEditMember !== null) {
    return {
      setCamera: state.databarang.setCamera,
      foto: state.datamember.showEditMember.dataimage,
      nama_customer: state.datamember.showEditMember.nama_customer,
      tgl_lahir: state.datamember.showEditMember.tgl_lahir,

      initialValues: {
        datagambarlama: state.datamember.showEditMember.datagambarlama,
        dataimage: state.datamember.showEditMember.dataimage,
        kode_member: state.datamember.showEditMember.kode_member,
        nama_customer: state.datamember.showEditMember.nama_customer,
        no_hp: state.datamember.showEditMember.no_hp,
        kecamatan: state.datamember.showEditMember.kecamatan,
        kelurahan: state.datamember.showEditMember.kelurahan,
        kota: state.datamember.showEditMember.kota,
        alamat_customer: state.datamember.showEditMember.alamat_customer,
        tgl_lahir: state.datamember.showEditMember.tgl_lahir === undefined ? getTglSystem() :state.datamember.showEditMember.tgl_lahir ,
      },
    };
  }
};
class FormTambahMember extends Component {
  constructor(props) {
    super(props);
    this.state = {
      perubahannama: false,
      tgl_lahir: new Date()
    };
    this.setStartDate = this.setStartDate.bind(this);

  }
  getValue = async (event) => {
    const file = event.target.files[0];
    const base64 = await convertBase64(file);
    this.props.dispatch(setCamera(base64));
    this.props.change("dataimage", base64);
  };
  setStartDate(date) {
    this.setState({
      tgl_lahir: new Date(date),
    });
  }

  componentDidMount() {
    setTimeout(() => {
      if (this.props.tgl_lahir === undefined) {
          this.setState(({
            tgl_lahir: new Date()
          }))
      } else {
        this.setState(({
          tgl_lahir: new Date(this.props.tgl_lahir)
        }))
      }
    }, 100);
  }
  otorisasi() {
    // console.log('Otorisasi');
    let id = document.getElementById("id_nama_customer").value;
    if (this.props.nama_customer === id) {
      this.setState({
        perubahannama: false,
      });
    } else {
      this.setState({
        perubahannama: true,
      });
    }
  }
  cekOtorisasi() {
    let id = document.getElementById("id_nama_customer").value;
    let username = document.getElementById('username').value;
    let password = document.getElementById('password').value;
    let hasil = {
      user_id: username,
      password: password,
      kategori: 'EDIT MEMBER',
      description: "GANTI USER DATA "+id,
      keterangan: "GANTI USER DATA "+id,
    };
    postData2("authorization", hasil)
      .then((resposn) => {
        ToastNotification("success", "Otorisasi Berhasil");
        this.setState({
          perubahannama: false,
        });
      }).catch((err) => {
        ToastNotification("info", "Username Atau Password Salah !!");
        this.setState({
          perubahannama: true,
        });
        document.getElementById('username').focus()
        this.props.change('password','')
      })
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4"></div>
          <div className="col-4 text-center">
            <img
              width="200px"
              height="200px"
              className="rounded-circle"
              src={
                this.props.foto === undefined
                  ? this.props.setCamera === false
                    ? userfound
                    : this.props.setCamera
                  : this.props.setCamera
                  ? this.props.setCamera
                  : this.props.foto
              }
              alt="userfound"
            />
            <br />
            <br />
            <br />
            <div className="input-group">
              <div className="custom-file">
                <input
                  type="file"
                  name="photo"
                  autoComplete="off"
                  className="custom-file-input"
                  id="exampleInputFile"
                  onChange={(e) => this.getValue(e)}
                />
                <label className="custom-file-label" htmlFor="exampleInputFile">
                  Pilih gambar
                </label>
              </div>
            </div>
          </div>
          <div className="col-4"></div>
          <div className="col-4">
            <Field name="kode_member" component={HiidenFiled} type="hidden" />
            <Field name="dataimage" component={HiidenFiled} type="hidden" />
            <Field name="datagambarlama" component={HiidenFiled} type="hidden" />
            {this.props.isEdit ? (
              <Field
                id="id_nama_customer"
                onChange={() => this.otorisasi()}
                name="nama_customer"
                component={ReanderField}
                type="text"
                label="Nama Customer"
                placeholder="Masukan Nama Customer"
              />
            ) : (
              <Field
                name="nama_customer"
                component={ReanderField}
                type="text"
                label="Nama Customer"
                placeholder="Masukan Nama Customer"
              />
            )}
          </div>
          {this.state.perubahannama ? (
            <>
              <div className="col-2">
                <br></br>
                <h3 style={{marginTop: "10px", textAlign: "center"}}>Otorisasi Owner</h3>
              </div>
              <div className="col-3">
                <Field
                  id="username"
                  name="username"
                  component={ReanderField}
                  nouperCase
                  type="text"
                  label="Masukan username"
                  placeholder="Masukan username"
                />
              </div>
              <div className="col-3">
                <Field
                  id="password"
                  onBlur={() => this.cekOtorisasi()}
                  name="password"
                  nouperCase
                  component={ReanderField}
                  type="password"
                  label="Masukan password"
                  placeholder="Masukan password"
                />
              </div>
            </>
          ) : null}

          <div className="col-4">
            <Field
              name="no_hp"
              component={ReanderField}
              type="number"
              label="No Hp"
              placeholder="Masukan No Hp"
            />
          </div>
          <div className="col-4">
            <Field
              name="kecamatan"
              component={ReanderField}
              type="text"
              label="Kecamatan"
              placeholder="Masukan Kecamatan"
            />
          </div>
          <div className="col-4">
            <Field
              name="kelurahan"
              component={ReanderField}
              type="text"
              label="Kelurahan"
              placeholder="Masukan Kelurahan"
            />
          </div>
          <div className="col-4">
            <Field
              name="kota"
              component={ReanderField}
              type="text"
              label="Kota"
              placeholder="Masukan Kota"
            />
          </div>
          <div className="col-4">
            <Field
              name="alamat_customer"
              component={ReanderField}
              type="text"
              label="Alamat"
              placeholder="Masukan Alamat"
            />
          </div>
          <div className="col-6">
            <Field
              name="tgl_lahir"
              component={InputDate}
              label="Tanggal Lahir"
              type="text"
              selected={this.state.tgl_lahir}
              onChange={(date) => this.setStartDate(date)}
            />
          </div>
          <div className="col-6">
            <label>&nbsp;</label>
            {this.state.perubahannama ? (
              <>
                <button
                  tabIndex="2"
                  className="btn btn-primary btn-block"
                  disabled={true}
                  type="button"
                >
                 Simpan Data
                </button>
              </>
            ) : (
              <button
                tabIndex="2"
                className="btn btn-primary btn-block"
                disabled={this.props.isLoading}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  "Simpan Data"
                )}
              </button>
            )}
          </div>
        </div>
      </form>
    );
  }
}

FormTambahMember = reduxForm({
  form: "FormTambahMember",
  enableReinitialize: true,
})(FormTambahMember);
export default connect(maptostate, null)(FormTambahMember);
